import {createStore} from 'redux'
import axios from "axios";


const initialState = {
    username: '',
    is_subscribed : false,
    activeRate : [],
    token: localStorage.getItem('token') || false,
}


function reducer(state = initialState, action) {
    const newState = {...state};

    switch (action.type) {

        case 'SET_USERNAME':
            newState.username = action.username
            break;

        case 'SET_TOKEN':
            axios.defaults.baseURL = process.env.REACT_APP_BACK_LOCAL_URL;

            axios.post('/users', {
                name: action.username,
                vk_id: action.vk_id,
                vk_user_id:  action.vk_user_id,
            }).then(res => {

                newState.username = action.username
                localStorage.setItem('token', res.data.token);
                newState.token = res.data.token;

                if(res.data.data.is_subscribed === 1)
                {
                    newState.is_subscribed = true;
                    newState.activeRate = res.data.data.rate;
                }
                axios.defaults.headers.common['Authorization'] = `Bearer ${res.data.token}`;

            }).catch(err => {});

            break;
    }
    return newState
}

const store = createStore(reducer)

export default store;
